.ai-chat {
  display: flex;
  flex-direction: column;
  flex: 1;
  min-height: 0;
  border-radius: var(--border-radius-md);
  overflow: hidden;
  background-color: var(--white);
  height: 100%;
}

.ai-chat.hidden {
  display: none;
}

.ai-chat.visible {
  display: flex;
}

.chat-messages {
  flex: 1;
  overflow-y: auto;
  padding: var(--spacing-md);
  min-height: 0;
}

.chat-input-container {
  flex-shrink: 0;
}

/*  */
@media (max-width: 768px) {
  .chat-messages {
    padding: var(--spacing-sm);
  }
}

/*  */
.message-content {
  max-width: 80%;
  padding: var(--spacing-sm) var(--spacing-md);
  border-radius: 18px;
  line-height: 1.4;
  word-wrap: break-word;
  overflow: hidden;
}

/* Markdown  */
.markdown-content {
  line-height: 1.6;
  overflow: visible;
  padding-right: var(--spacing-sm);
  max-width: 100%;
}

/*  markdown  */
.markdown-content::-webkit-scrollbar {
  width: 4px;
}

.markdown-content::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.1);
  border-radius: 2px;
}

.markdown-content::-webkit-scrollbar-thumb {
  background: rgba(0, 0, 0, 0.2);
  border-radius: 2px;
}

.markdown-content::-webkit-scrollbar-thumb:hover {
  background: rgba(0, 0, 0, 0.3);
}

.welcome-message {
  text-align: center;
  color: var(--gray-600);
  padding: var(--spacing-lg);
  font-style: italic;
}

.message {
  margin-bottom: var(--spacing-md);
  display: flex;
}

.user-message {
  justify-content: flex-end;
}

.assistant-message {
  justify-content: flex-start;
}

.user-message .message-content {
  background-color: var(--primary-color);
  color: var(--white);
  border-bottom-right-radius: 4px;
}

.assistant-message .message-content {
  background-color: var(--gray-200);
  color: var(--gray-800);
  border-bottom-left-radius: 4px;
}

.typing-indicator {
  display: flex;
  align-items: center;
  padding: var(--spacing-sm) var(--spacing-md);
}

.typing-indicator span {
  height: 8px;
  width: 8px;
  background-color: var(--gray-600);
  border-radius: var(--border-radius-full);
  display: inline-block;
  margin: 0 2px;
  animation: typing 1s infinite;
}

.typing-indicator span:nth-child(2) {
  animation-delay: 0.2s;
}

.typing-indicator span:nth-child(3) {
  animation-delay: 0.4s;
}

@keyframes typing {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-5px);
  }
}

.chat-input-container {
  display: flex;
  flex-direction: column;
  background-color: var(--white);
  padding: var(--spacing-sm);
  flex-shrink: 0;
}

@media (max-width: 768px) {
  .ai-chat {
    border-radius: 0;
    height: 100%;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    width: 100%;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }
  
  .chat-messages {
    flex: 1;
    padding: var(--spacing-sm);
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    min-height: 0;
  }
  
  .message-content {
    max-width: 90%;
    font-size: var(--font-size-sm);
    padding: var(--spacing-sm);
  }
  
  .chat-input-container {
    padding: var(--spacing-sm);
    background-color: var(--white);
    border-top: 1px solid var(--gray-300);
    display: flex;
    flex-direction: column;
    width: 100%;
    box-sizing: border-box;
    left: 0;
    right: 0;
    margin: 0;
    position: relative;
  }
}

/*  */
.chat-messages::-webkit-scrollbar {
  width: 0px;
  background: transparent;
}

.markdown-content::-webkit-scrollbar {
  width: 0px;
  background: transparent;
}

/* Firefox */
.chat-messages {
  scrollbar-width: none;
  -ms-overflow-style: none;
}

.markdown-content {
  scrollbar-width: none;
  -ms-overflow-style: none;
}`, "",{"version":3,"sources":["webpack://./src/styles/chat/chat-container.css"],"names":[],"mappings":"AAAA,WAAW;;AAEX;EACE,aAAa;EACb,sBAAsB;EACtB,OAAO;EACP,aAAa;EACb,sCAAsC;EACtC,gBAAgB;EAChB,8BAA8B;EAC9B,YAAY;AACd;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,OAAO;EACP,gBAAgB;EAChB,0BAA0B;EAC1B,aAAa;AACf;;AAEA;EACE,cAAc;AAChB;;AAEA,gBAAgB;AAChB;EACE;IACE,0BAA0B;EAC5B;AACF;;AAEA,iBAAiB;AACjB;EACE,cAAc;EACd,4CAA4C;EAC5C,mBAAmB;EACnB,gBAAgB;EAChB,qBAAqB;EACrB,gBAAgB;AAClB;;AAEA,sBAAsB;AACtB;EACE,gBAAgB;EAChB,iBAAiB;EACjB,gCAAgC;EAChC,eAAe;AACjB;;AAEA,8BAA8B;AAC9B;EACE,UAAU;AACZ;;AAEA;EACE,8BAA8B;EAC9B,kBAAkB;AACpB;;AAEA;EACE,8BAA8B;EAC9B,kBAAkB;AACpB;;AAEA;EACE,8BAA8B;AAChC;;AAEA;EACE,kBAAkB;EAClB,sBAAsB;EACtB,0BAA0B;EAC1B,kBAAkB;AACpB;;AAEA;EACE,gCAAgC;EAChC,aAAa;AACf;;AAEA;EACE,yBAAyB;AAC3B;;AAEA;EACE,2BAA2B;AAC7B;;AAEA;EACE,sCAAsC;EACtC,mBAAmB;EACnB,+BAA+B;AACjC;;AAEA;EACE,iCAAiC;EACjC,sBAAsB;EACtB,8BAA8B;AAChC;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,4CAA4C;AAC9C;;AAEA;EACE,WAAW;EACX,UAAU;EACV,iCAAiC;EACjC,wCAAwC;EACxC,qBAAqB;EACrB,aAAa;EACb,6BAA6B;AAC/B;;AAEA;EACE,qBAAqB;AACvB;;AAEA;EACE,qBAAqB;AACvB;;AAEA;EACE;IACE,wBAAwB;EAC1B;EACA;IACE,2BAA2B;EAC7B;AACF;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,8BAA8B;EAC9B,0BAA0B;EAC1B,cAAc;AAChB;;AAEA;EACE;IACE,gBAAgB;IAChB,YAAY;IACZ,aAAa;IACb,sBAAsB;IACtB,gBAAgB;IAChB,WAAW;IACX,sBAAsB;IACtB,SAAS;IACT,UAAU;EACZ;;EAEA;IACE,OAAO;IACP,0BAA0B;IAC1B,gBAAgB;IAChB,iCAAiC;IACjC,aAAa;EACf;;EAEA;IACE,cAAc;IACd,8BAA8B;IAC9B,0BAA0B;EAC5B;;EAEA;IACE,0BAA0B;IAC1B,8BAA8B;IAC9B,qCAAqC;IACrC,aAAa;IACb,sBAAsB;IACtB,WAAW;IACX,sBAAsB;IACtB,OAAO;IACP,QAAQ;IACR,SAAS;IACT,kBAAkB;EACpB;AACF;;AAEA,mBAAmB;AACnB;EACE,UAAU;EACV,uBAAuB;AACzB;;AAEA;EACE,UAAU;EACV,uBAAuB;AACzB;;AAEA,iBAAiB;AACjB;EACE,qBAAqB;EACrB,wBAAwB;AAC1B;;AAEA;EACE,qBAAqB;EACrB,wBAAwB;AAC1B","sourcesContent":["/*  */\n\n.ai-chat {\n  display: flex;\n  flex-direction: column;\n  flex: 1;\n  min-height: 0;\n  border-radius: var(--border-radius-md);\n  overflow: hidden;\n  background-color: v