.question-panel {
  padding: var(--spacing-lg);
  border-bottom: 1px solid var(--gray-300);
  max-height: 250px;
  overflow-y: auto;
  border-radius: var(--border-radius-md);
  background-color: var(--gray-100);
  margin-bottom: var(--spacing-md);
}

.question-tabs {
  display: flex;
  border-bottom: 1px solid var(--gray-300);
  margin-bottom: var(--spacing-md);
  overflow-x: auto;
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.question-tabs::-webkit-scrollbar {
  display: none;
}

.tab-button {
  padding: var(--spacing-sm) var(--spacing-md);
  cursor: pointer;
  background-color: transparent;
  border: none;
  border-bottom: 2px solid transparent;
  color: var(--gray-600);
  font-size: var(--font-size-md);
  white-space: nowrap;
  transition: color var(--transition-normal), border-color var(--transition-normal);
}

.tab-button.active {
  color: var(--primary-color);
  font-weight: 600;
}

.tab-button:hover {
  color: var(--primary-color);
  background-color: transparent;
}

.question-content {
  height: calc(100% - 48px);
  overflow-y: auto;
  padding-right: var(--spacing-sm);
}

.question-group {
  margin-bottom: var(--spacing-md);
}

.category-heading {
  font-size: var(--font-size-sm);
  font-weight: 600;
  color: var(--gray-800);
  margin-bottom: var(--spacing-sm);
  margin-top: 0;
}

.questions-wrapper {
  display: flex;
  flex-wrap: wrap;
  gap: var(--spacing-sm);
}

.question-text {
  background-color: var(--gray-100);
  border-radius: 12px;
  padding: var(--spacing-sm) var(--spacing-md);
  font-size: var(--font-size-sm);
  cursor: pointer;
  color: var(--gray-800);
  line-height: 1.4;
  margin: 0;
  transition: background-color var(--transition-normal);
}

.question-text:hover {
  background-color: var(--gray-200);
}

@media (max-width: 768px) {
  .question-panel {
    padding: var(--spacing-sm);
    margin-bottom: var(--spacing-sm);
    max-height: 120px;
    flex-shrink: 0;
  }
  
  .tab-button {
    padding: 6px 10px;
    font-size: 13px;
  }
  
  .question-text {
    font-size: 12px;
    padding: 4px 8px;
  }
}

/*  */
.question-content::-webkit-scrollbar {
  width: 0px;
  background: transparent;
}

.question-panel::-webkit-scrollbar {
  width: 0px;
  background: transparent;
}

/* Firefox */
.question-content {
  scrollbar-width: none;
  -ms-overflow-style: none;
}

.question-panel {
  scrollbar-width: none;
  -ms-overflow-style: none;
}`, "",{"version":3,"sources":["webpack://./src/styles/chat/question-panel.css"],"names":[],"mappings":"AAAA,aAAa;;AAEb;EACE,0BAA0B;EAC1B,wCAAwC;EACxC,iBAAiB;EACjB,gBAAgB;EAChB,sCAAsC;EACtC,iCAAiC;EACjC,gCAAgC;AAClC;;AAEA;EACE,aAAa;EACb,wCAAwC;EACxC,gCAAgC;EAChC,gBAAgB;EAChB,wBAAwB;EACxB,qBAAqB;AACvB;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,4CAA4C;EAC5C,eAAe;EACf,6BAA6B;EAC7B,YAAY;EACZ,oCAAoC;EACpC,sBAAsB;EACtB,8BAA8B;EAC9B,mBAAmB;EACnB,iFAAiF;AACnF;;AAEA;EACE,2BAA2B;EAC3B,gBAAgB;AAClB;;AAEA;EACE,2BAA2B;EAC3B,6BAA6B;AAC/B;;AAEA;EACE,yBAAyB;EACzB,gBAAgB;EAChB,gCAAgC;AAClC;;AAEA;EACE,gCAAgC;AAClC;;AAEA;EACE,8BAA8B;EAC9B,gBAAgB;EAChB,sBAAsB;EACtB,gCAAgC;EAChC,aAAa;AACf;;AAEA;EACE,aAAa;EACb,eAAe;EACf,sBAAsB;AACxB;;AAEA;EACE,iCAAiC;EACjC,mBAAmB;EACnB,4CAA4C;EAC5C,8BAA8B;EAC9B,eAAe;EACf,sBAAsB;EACtB,gBAAgB;EAChB,SAAS;EACT,qDAAqD;AACvD;;AAEA;EACE,iCAAiC;AACnC;;AAEA;EACE;IACE,0BAA0B;IAC1B,gCAAgC;IAChC,iBAAiB;IACjB,cAAc;EAChB;;EAEA;IACE,iBAAiB;IACjB,eAAe;EACjB;;EAEA;IACE,eAAe;IACf,gBAAgB;EAClB;AACF;;AAEA,mBAAmB;AACnB;EACE,UAAU;EACV,uBAAuB;AACzB;;AAEA;EACE,UAAU;EACV,uBAAuB;AACzB;;AAEA,iBAAiB;AACjB;EACE,qBAAqB;EACrB,wBAAwB;AAC1B;;AAEA;EACE,qBAAqB;EACrB,wBAAwB;AAC1B","sourcesContent":["/*  */\n\n.question-panel {\n  padding: var(--spacing-lg);\n  border-bottom: 1px solid var(--gray-300);\n  max-height: 250px;\n  overflow-y: auto;\n  border-radius: var(--border-radius-md);\n  background-color: var(--gray-100);\n  margin-bottom: var(--spacing-md);\n}\n\n.question-tabs {\n  display: flex;\n  border-bottom: 1px solid var(--gray-300);\n  margin-bottom: var(--spacing-md);\n  overflow-x: auto;\n  -ms-overflow-style: none;\n  scrollbar-width: none;\n}\n\n.question-tabs::-webkit-scrollbar {\n  display: none;\n}\n\n.tab-button {\n  padding: var(--spacing-sm) var(--spacing-md);\n  cursor: pointer;\n  background-color: transparent;\n  border: none;\n  border-bottom: 2px solid transparent;\n  color: var(--gray-600);\n  font-size: var(--font-size-md);\n  white-space: nowrap;\n  transition: color var(