.chat-input {
  display: flex;
  padding: 0;
  gap: var(--spacing-md);
  align-items: center;
}

.chat-input input {
  flex: 1;
  padding: var(--spacing-sm) var(--spacing-lg);
  border: 1px solid var(--gray-400);
  border-radius: 24px;
  font-size: var(--font-size-md);
  outline: none;
  transition: all var(--transition-normal) ease;
  height: 40px;
  box-sizing: border-box;
}

.chat-input input:focus {
  border-color: var(--primary-color);
  box-shadow: 0 0 0 2px rgba(0, 123, 255, 0.25);
}

.chat-action-button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  padding: 0;
  background-color: var(--primary-color);
  color: var(--white);
  border: none;
  border-radius: var(--border-radius-full);
  cursor: pointer;
  font-size: 20px;
  line-height: 40px;
  transition: all var(--transition-normal) ease;
  flex-shrink: 0;
  box-shadow: 0 2px 5px rgba(0, 123, 255, 0.3);
  margin-top: 0;
}

.chat-action-button svg {
  width: 20px;
  height: 20px;
  line-height: 1;
}

.chat-action-button:hover:not(:disabled) {
  background-color: var(--primary-hover);
}

.chat-action-button:disabled {
  background-color: var(--gray-400);
  cursor: not-allowed;
}

@media (max-width: 768px) {
  .chat-input {
    flex-shrink: 0;
    margin-top: auto;
  }
  
  .chat-input input {
    font-size: var(--font-size-sm);
    height: 36px;
    padding: var(--spacing-sm) var(--spacing-md);
  }
  
  .chat-action-button {
    width: 36px;
    height: 36px;
  }
}`, "",{"version":3,"sources":["webpack://./src/styles/chat/chat-input.css"],"names":[],"mappings":"AAAA,aAAa;;AAEb;EACE,aAAa;EACb,UAAU;EACV,sBAAsB;EACtB,mBAAmB;AACrB;;AAEA;EACE,OAAO;EACP,4CAA4C;EAC5C,iCAAiC;EACjC,mBAAmB;EACnB,8BAA8B;EAC9B,aAAa;EACb,6CAA6C;EAC7C,YAAY;EACZ,sBAAsB;AACxB;;AAEA;EACE,kCAAkC;EAClC,6CAA6C;AAC/C;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,WAAW;EACX,YAAY;EACZ,UAAU;EACV,sCAAsC;EACtC,mBAAmB;EACnB,YAAY;EACZ,wCAAwC;EACxC,eAAe;EACf,eAAe;EACf,iBAAiB;EACjB,6CAA6C;EAC7C,cAAc;EACd,4CAA4C;EAC5C,aAAa;AACf;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,cAAc;AAChB;;AAEA;EACE,sCAAsC;AACxC;;AAEA;EACE,iCAAiC;EACjC,mBAAmB;AACrB;;AAEA;EACE;IACE,cAAc;IACd,gBAAgB;EAClB;;EAEA;IACE,8BAA8B;IAC9B,YAAY;IACZ,4CAA4C;EAC9C;;EAEA;IACE,WAAW;IACX,YAAY;EACd;AACF","sourcesContent":["/*  */\n\n.chat-input {\n  display: flex;\n  padding: 0;\n  gap: var(--spacing-md);\n  align-items: center;\n}\n\n.chat-input input {\n  flex: 1;\n  padding: var(--spacing-sm) var(--spacing-lg);\n  border: 1px solid var(--gray-400);\n  border-radius: 24px;\n  font-size: var(--font-size-md);\n  outline: none;\n  transition: all var(--transition-normal) ease;\n  height: 40px;\n  box-sizing: border-box;\n}\n\n.chat-input input:focus {\n  border-color: var(--primary-color);\n  box-shadow: 0 0 0 2px rgba(0, 123, 255, 0.25);\n}\n\n.chat-action-button {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  width: 40px;\n  height: 40px;\n  padding: 0;\n  background-color: var(--primary-color);\n  color: var(--white);\n  border: none;\n  border-radius: var(--border-radius-full);\n  cursor: pointer;\n  font-size: 20px;\n  line-height: 40px;\n  transition: all var(--transition-normal) ease;\n  flex-shrink: 0;\n  box-shadow: 0 2px 5px rgba(0, 123, 255, 0.3);\n  margin-top: 0;\n}\n\n.chat-action-button svg {\n  width: 20px;\n  height: 20px;\n  line-height: 1;\n}\n\n.chat-action-button:hover:not(:disabled) {\n  background-color: var(--primary-hover);\n}\n\n.chat-action-button:disabled {\n  background-co