___CSS_LOADER_EXPORT___.push([module.id, `/* Base styles restored, with the critical box-sizing fix */

.segmented-control {
  display: inline-flex;
  background-color: var(--gray-200);
  border-radius: var(--border-radius-md);
  overflow: hidden;
  padding: 2px;
  gap: 2px;
  position: relative;
}

.segmented-control-slider {
  position: absolute;
  top: 2px;
  height: calc(100% - 4px);
  background-color: var(--iztro-color-active);
  border-radius: calc(var(--border-radius-md) - 2px);
  transition: width var(--transition-normal) ease, left var(--transition-normal) ease, opacity var(--transition-normal) ease;
  z-index: 1;
  box-shadow: var(--box-shadow-sm);
}

.segmented-control-label {
  text-align: center;
  position: relative;
  display: block;
  margin-bottom: 0;
  z-index: 2;
  flex: 0 0 auto;
}

.segmented-control-input {
  position: absolute;
  opacity: 0;
  width: 0;
  height: 0;
}

.segmented-control-button {
  display: block;
  padding: 6px 16px; /* Reduced vertical padding */
  color: var(--gray-800);
  cursor: pointer;
  border-radius: calc(var(--border-radius-md) - 2px);
  transition: color var(--transition-normal) ease;
  user-select: none;
  position: relative;
  z-index: 2;
  font-size: var(--font-size-md);
  /* min-height is removed to let padding and font-size control the height */
  display: flex;
  align-items: center;
  justify-content: center;
  -webkit-tap-highlight-color: transparent;
  white-space: nowrap;
  flex: 0 0 auto;
  /* The line below is intentionally removed as per user feedback */
  /* box-sizing: border-box; */
  /*  */
  touch-action: manipulation;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -webkit-tap-highlight-color: transparent;
  /*  */
  transform: translateZ(0);
  -webkit-transform: translateZ(0);
  /*  */
  -webkit-font-smoothing: antialiased;
  /*  */
  min-height: 36px;
  /*  */
  touch-action: manipulation;
}

/*  */
@media (max-width: 768px) {
  .segmented-control-button {
    /*  */
    min-height: 36px;
    /*  */
    padding: 8px 16px;
    /*  */
    touch-action: manipulation;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -webkit-tap-highlight-color: transparent;
  }
}

.segmented-control-input:checked + .segmented-control-button {
  color: var(--white);
  font-weight: 500;
}
