.form-group {
  margin-bottom: var(--spacing-lg);
}

/*  */
@media (max-width: 768px) {
  .form-group {
    margin-bottom: var(--spacing-md);
  }
}

.form-group label {
  display: block;
  margin-bottom: var(--spacing-xs);
  font-weight: 600;
  color: var(--gray-700);
  font-size: var(--font-size-sm);
}

.form-group-segmented label {
  margin-bottom: var(--spacing-xs);
  font-weight: 600;
  color: var(--gray-700);
  font-size: var(--font-size-sm);
}

.form-group-segmented {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 0;
}

.form-group-segmented .segmented-control {
  margin: 0;
  width: auto;
}

.form-group input,
.form-group select {
  width: 100%;
  padding: var(--spacing-md);
  border: 1px solid var(--gray-400);
  border-radius: var(--border-radius-sm);
  font-size: var(--font-size-md);
  box-sizing: border-box;
}

/*  */
@media (max-width: 768px) {
  .form-group input,
  .form-group select {
    padding: var(--spacing-md);
    font-size: var(--font-size-md);
    border-radius: var(--border-radius-md);
    border: 1px solid var(--gray-300);
    background-color: var(--white);
    /*  */
    min-height: 44px;
    /* iOS */
    touch-action: manipulation;
    /*  */
    margin-bottom: var(--spacing-xs);
    /*  */
    box-sizing: border-box;
    /*  */
    -webkit-tap-highlight-color: transparent;
    /*  */
    font-size: 16px;
  }
  
  .form-group input:focus,
  .form-group select:focus {
    border-color: var(--iztro-color-active);
    outline: none;
    box-shadow: 0 0 0 2px rgba(24, 144, 255, 0.2);
    background-color: var(--white);
  }
  
  /*  */
  .form-group select {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-repeat: no-repeat;
    background-position: right 10px center;
    background-size: 16px;
    padding-right: 35px;
  }
  
  /*  */
  .form-group input[type="number"] {
    -moz-appearance: textfield;
  }
  
  .form-group input[type="number"]::-webkit-outer-spin-button,
  .form-group input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }
}

.form-group input:focus,
.form-group select:focus {
  border-color: var(--primary-color);
  outline: none;
  box-shadow: 0 0 0 2px rgba(0, 123, 255, 0.25);
}

.form-group-inline {
  display: flex;
  gap: var(--spacing-lg);
  margin-bottom: var(--spacing-lg);
}

/*  */
@media (max-width: 768px) {
  .form-group-inline {
    flex-direction: column;
    gap: var(--spacing-sm);
    margin-bottom: var(--spacing-md);
  }
  
  .form-group-inline .form-group {
    margin-bottom: 0;
  }
}

.form-group-inline .form-group {
  flex: 1;
  margin-bottom: 0;
}

.form-group-inline .form-group input {
  width: 100%;
}

/*  */
@media (max-width: 768px) {
  .form-group-inline-mobile {
    display: flex;
    flex-direction: row;
    gap: var(--spacing-sm);
    margin-bottom: var(--spacing-md);
  }
  
  .form-group-inline-mobile .form-group {
    flex: 1;
    margin-bottom: 0;
  }
  
  .form-group-inline-mobile .form-group label {
    font-size: var(--font-size-xs);
    margin-bottom: var(--spacing-xs);
  }
}

/*  */
@media (max-width: 768px) {
  .form-group-segmented {
    /*  */
    min-height: 36px;
    /*  */
    padding: 8px 16px;
    /*  */
    touch-action: manipulation;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -webkit-tap-highlight-color: transparent;
  }
}
